import React, { PropTypes } from 'react';
import { Link } from 'react-router';
import styled from 'styled-components';

import ProgressBar from './order/ProgressBar';
import InfosBlock from '../components/InfosBlock';
import { Wrapper, Block, Button3, Title2, Header2, Logo, Center, P, colors, mobileThresholdPixels }
  from '../components/styledComponents';
import LogoImg from '../assets/logo1.png';

const Message = styled.h2`
  font-weight: 200;
  font-size: 26px;
  margin-bottom: 20px;
  margin-top: 20px;
  color: ${colors.navy};
  text-align: center;
  @media (max-width: ${mobileThresholdPixels}) {
    margin-bottom: 3px;
    margin-top: 5px;
    font-size: 20px;
  }
`;

const renderRegisterSuccess = () => (
  <div>
    <Message>Votre inscription et votre commande ont bien t prises en compte</Message>
    <Center>
      <Link to={'/'}><Button3>{"Revenir vers la page d'accueil"}</Button3></Link>
    </Center>
  </div>
);

const renderLoggedInSuccess = () => (
  <div>
    <Message>Votre commande a bien t prise en compte</Message>
    <Center>
      <Link to={'/'}><Button3>{"Revenir vers la page d'accueil"}</Button3></Link>
    </Center>
  </div>
);

const Step5 = ({ submitRegister, login, isLoading, successRegisterOrder, error, total, customer, isLoggued, errorLogin,
isLoginLoading, submitLoggedInOrder, successLoggedInOrder, rdv }) => (
  <Wrapper>
    <Block id="infos">
      <Header2>
        <Logo src={LogoImg} />
        <ProgressBar step={4} />
        {!successRegisterOrder &&
          <Title2>Mes informations</Title2>
        }
      </Header2>
      {isLoading && <Center><P>Chargement</P></Center>}
      {successLoggedInOrder && renderLoggedInSuccess()}
      {successRegisterOrder && renderRegisterSuccess()}
      {error && <Center><P>Une erreur a eu lieu</P></Center>}
      {!successRegisterOrder && !successLoggedInOrder && !isLoading && !error &&
        <InfosBlock
          total={total}
          submitRegister={submitRegister}
          login={login}
          customer={customer}
          isLoggued={isLoggued}
          errorLogin={errorLogin}
          isLoginLoading={isLoginLoading}
          submitLoggedInOrder={submitLoggedInOrder}
          rdv={rdv}
        />
      }
    </Block>
  </Wrapper>
);

Step5.propTypes = {
  submitRegister: PropTypes.func.isRequired,
  submitLoggedInOrder: PropTypes.func.isRequired,
  login: PropTypes.func.isRequired,
  isLoading: PropTypes.bool,
  successRegisterOrder: PropTypes.bool,
  successLoggedInOrder: PropTypes.bool,
  error: PropTypes.bool,
  errorLogin: PropTypes.bool,
  isLoggued: PropTypes.bool,
  isLoginLoading: PropTypes.bool,
  total: PropTypes.number.isRequired,
  customer: PropTypes.shape({}),
  rdv: PropTypes.shape({}).isRequired,
};

Step5.defaultProps = {
  isLoading: false,
  successRegisterOrder: false,
  successLoggedInOrder: false,
  error: false,
  errorLogin: false,
  isLoggued: false,
  isLoginLoading: false,
  customer: {},
};

export default Step5;



