import React, { Component, PropTypes } from 'react';
import { connect } from 'react-redux';
import Step5 from '../../components/Step5';
import callApi from '../../services/api';
import { getTotal } from './orderReducer';
import { pricingInverse } from '../../pricing';
import { registerCard } from '../../services/mangopay';

class Step5Container extends Component {
  constructor() {
    super();
    this.state = {};
    this.submitRegister = this.submitRegister.bind(this);
    this.submitLoggedInOrder = this.submitLoggedInOrder.bind(this);
    this.login = this.login.bind(this);
  }

  createRequest(customer) {
    const order = {
      rdv1: { begin: this.props.rdv.toDate(), end: this.props.rdv.add(30, 'minutes').toDate() },
      fabrics: this.props.fabrics,
      clothes: this.props.clothes.map(cloth => ({
        ...cloth,
        items: Object.keys(cloth.alterations).map(alterationId => ({
          id: alterationId,
          quantity: cloth.alterations[alterationId],
          price: pricingInverse[alterationId].price,
        })),
      })),
    };
    return { customer, order };
  }

  submitCard(cardData, order, customer) {
    if (cardData.cardId) {
      return callApi('public/submitCard', 'post',
        { card: { CardId: cardData.cardId }, orderId: order._id }) // eslint-disable-line no-underscore-dangle
      .then((res) => {
        this.setState({ redirecting: true });
        if (res.redirectUrl) {
          window.location = res.redirectUrl;
        }
      });
    }
    return registerCard(cardData, customer._id) // eslint-disable-line no-underscore-dangle
    .then(card =>
      callApi('public/submitCard', 'post', { card, orderId: order._id })) // eslint-disable-line no-underscore-dangle
    .then((res) => {
      this.setState({ redirecting: true });
      if (res.redirectUrl) {
        window.location = res.redirectUrl;
      }
    });
  }

  submitRegister(customerReq, cardData) {
    this.setState({ isLoading: true });
    callApi('registerAndOrder', 'post', this.createRequest(customerReq))
    .then(({ order, customer }) => this.submitCard(cardData, order, customer))
    .catch(() => this.setState({ error: true, isLoading: false }));
  }

  submitLoggedInOrder(customerReq, cardData) {
    this.setState({ isLoading: true });
    callApi('editCustomerAndOrder', 'post', this.createRequest(customerReq))
    .then(({ order, customer }) => this.submitCard(cardData, order, customer))
    .catch(() => this.setState({ error: true, isLoading: false }));
  }

  login(email, password) {
    this.setState({ isLoginLoading: true });
    callApi('login', 'post', { email, password }).then((res) => {
      this.setState({ isLoggued: true, isLoginLoading: false, customer: res.customer });
    })
    .catch(() => this.setState({ errorLogin: true, isLoginLoading: false }));
  }

  render() {
    return (
      <Step5
        submitRegister={this.submitRegister}
        submitLoggedInOrder={this.submitLoggedInOrder}
        login={this.login}
        isLoading={this.state.isLoading}
        isLoginLoading={this.state.isLoginLoading}
        successRegisterOrder={this.state.successRegisterOrder}
        successLoggedInOrder={this.state.successLoggedInOrder}
        error={this.state.error}
        errorLogin={this.state.errorLogin}
        total={this.props.total}
        customer={this.state.customer}
        isLoggued={this.state.isLoggued}
        rdv={this.props.rdv}
      />
    );
  }
}

Step5Container.propTypes = {
  rdv: PropTypes.shape({ add: PropTypes.func, toDate: PropTypes.func }).isRequired,
  clothes: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
  fabrics: PropTypes.arrayOf(PropTypes.string).isRequired,
  total: PropTypes.number.isRequired,
};

const mapStateToProps = state => ({
  rdv: state.order.rdv,
  clothes: state.order.clothes,
  fabrics: state.order.fabrics,
  total: getTotal(state),
});
const mapDispatchToProps = dispatch => ({ dispatch });
export default connect(mapStateToProps, mapDispatchToProps)(Step5Container);



