import React, { PropTypes } from 'react';
import CardReactFormContainer from 'card-react';
import { Input, Form1, Row } from './styledComponents';

const CreditCard = ({ onChange }) => (
  <CardReactFormContainer
    container="card-wrapper"
    formInputsNames={{
      number: 'CCnumber',
      expiry: 'CCexpiry',
      cvc: 'CCcvc',
      name: 'CCname',
    }}
    initialValues={{ name: ' ' }}
    classes={{
      valid: 'valid-input', // optional  default 'jp-card-valid'
      invalid: 'invalid-input', // optional  default 'jp-card-invalid'
    }}
    formatting // specify whether you want to format the form inputs or not
  >

    <div id="card-wrapper" />
    <Form1>
      <Input
        placeholder="Numro de carte" type="text" name="CCnumber"
        onChange={e => onChange('cardNumber', e.target.value)}
      />
      <Row>
        <Input
          half placeholder="MM/AA" type="text" name="CCexpiry"
          onChange={e => onChange('expiryDate', e.target.value)}
        />
        <Input
          half placeholder="CVC" type="text" name="CCcvc"
          onChange={e => onChange('cvc', e.target.value)}
        />
      </Row>
    </Form1>

  </CardReactFormContainer>
);

CreditCard.propTypes = {
  onChange: PropTypes.func.isRequired,
};

export default CreditCard;



